<template>
  <div>
    <v-container fill-height style="max-width:450px">
      <v-layout align-center row wrap>
        <v-flex xs12>
          <v-alert
            class="mb-3"
            :value="isLoginError"
            dense
            outlined
            type="error"
          >
            변경할 비밀번호를 적어주세요
          </v-alert>

          <v-alert class="mb-3" :value="isLogin" type="success">
            변경 완료
          </v-alert>

          <v-card>
            <div class="pa-3">
              <v-toolbar flat height="30">
                <v-toolbar-title>비밀번호 변경</v-toolbar-title>
              </v-toolbar>
            </div>
            <v-text-field
              type="text"
              v-model="engineer_PW_reset1"
              label="비밀번호를 입력하세요"
            >
            </v-text-field>
            <v-text-field
              v-model="engineer_PW_reset2"
              type="password"
              label="비밀번호를 재입력하세요"
            >
            </v-text-field>

            <v-btn color="green" depressed large block @click="changePW"
              >비밀번호 변경</v-btn
            >
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      items: [],
      engineer_PW_reset1: "",
      engineer_PW_reset2: "",
    };
  },
  methods: {
    submit: function() {
      if (engineer_PW_reset1 != engineer_PW_reset2) {
        alert("입력한 비밀번호 값이 다릅니다.");
      } else {
        var params = new URLSearchParams();
        params.append("engineer_PW_reset", this.engineer_PW_reset2);
        axios
          .post("http://172.31.36.58/ind/changPW", params)
          .then((res) => {
            console.log(res.data);

            this.items = res.data;
            alert("비밀번호 변경이 완료되었습니다.");
            next("/"); //홈으로 보냄
          })
          .catch(function(err) {
            console.log(err);
          });
      }
    },
  },
};
</script>
